.row {
    display: -ms-flexbox; /* IE 10 */
    display: flex;
    -ms-flex-wrap: wrap; /* IE 10 */
    flex-wrap: wrap;
    padding: 0 4px;
  }
  
  /* Create two equal columns that sits next to each other */
  .column {
    -ms-flex: 50%; /* IE 10 */
    flex: 50%;
    padding: 0 4px;
  }
  
  .column .ModalImage {
    margin-top: 8px;
    vertical-align: middle;
    padding: 10px 10px;
    border-radius: 29px;
    cursor: pointer;
    transition: 0.3s;
    float: left;
    width: 30%;
  }
.column .ModalImage:hover {
      background-color:rgb(78, 0, 0);
      opacity: 0.8;
  }

